h1#menu {
  display: flex;
  justify-content: center;
}

ul#menuList {
  display: grid;
  grid-template-areas: 'a a';
  grid-auto-rows: 250px;
  grid-gap: 20px;
  margin: 20px auto;
  padding: 0;
  overflow-y: scroll;
  padding: 30px;
  background: #f3f3f3;
  height: auto;
}

li.item {
  position: relative;
  width: auto;
  height: 100%;
  background: #f3f3f3;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 20px 10px;
  min-width: 168px;
  box-shadow: 0px 0px 5px #505050
}

@media only screen and (min-width: 320px) and (max-width: 480px) {
  ul#menuList {
    grid-template-areas: 'a';
    margin-bottom: 0;
    max-height: 440px;
  }
}

@media only screen and (min-width: 481px) and (max-width: 600px) {
  ul#menuList {
    grid-template-areas: 'a a';
