#cartList {
  margin: 87px auto;
  padding: 0;
  list-style: none;
  min-width: 90%;
  background: #f3f3f3;
  height: fit-content;
  max-height: calc(100vh - 80px);
  overflow-y: hidden;
  box-shadow: 0px 0px 5px #505050;
}

#title h4 {
  margin: 0;
  padding: 15px 0;
}

#cartList li {
  border-bottom: 1px solid #b3b3b3;
  display: flex;
  align-items: center;
  padding: 10px;
}

.itemInfo {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-self: baseline;
}

.itemName {
  clear: both;
}

.itemName:first-letter {
  text-transform: uppercase;
}

.itemPrice {
  float: right;
}

#total {
  display: flex;
  justify-content: space-between;
  padding: 5px 10px 5px !important;
}

.removeButton {
  padding: 0 !important;
  border: none;
  margin: 4px auto;
  width: 80%;
}

.removeButton {
  width: 40%;
  height: 40px;
  background: linear-gradient(to bottom, #f30101 0%, #f30303 100%);
  border: none;
  border-radius: 5px;
  position: relative;
  border-bottom: 4px solid #a90505;
  color: #fbfbfb;
  font-weight: 600;
  font-family: 'Open Sans', sans-serif;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.4);
  font-size: 15px;
  text-align: left;
  text-indent: 5px;
  cursor: pointer;
}

.removeButton:after {
  content: '';
  width: 0;
  height: 0;
  display: block;
  border-top: 20px solid #a90505;
  border-bottom: 20px solid #a90505;
  border-left: 16px solid transparent;
  border-right: 20px solid #a90505;
  position: absolute;
  opacity: 0.6;
  right: 0;
  top: 0;
  border-radius: 0 5px 5px 0;
}

.removeButton:disabled {
  filter: grayscale(100%);
  cursor: initial;
}

@media only screen and (min-width: 320px) and (max-width: 480px) {
  #cartList {
    margin: 20px 0 0 0;
    max-height: 400px;
  }
}

@media only screen and (min-width: 481px) and (max-width: 600px) {
  #cartList {
    margin: 20px 0 0 0;
    max-height: 400px;
  }
}

@media only screen and (min-width: 601px) and (max-width: 768px) {
  #cartList {
    margin: 20px 0 0 0;
    max-height: 400px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 900px) {
  #cartList {
    margin: 20px 0 0 0;
    max-height: 400px;
  }
}

@media only screen and (min-width: 901px) and (max-width: 1024px) {
  #cartList {
    margin: 20px 0 0 0;
    max-height: 400px;
  }
}

@media only screen and (min-width: 1025px) and (max-width: 1200px) {
  #cartList {
    margin: 60px 0;
    max-height: calc(100vh - 80px);
    max-width: 90%;
  }
}
